
          
let arr =[];

function task(val){
    arr.push(val);
    let fun =document.getElementById('display');
    fun.innerText=arr.join('');   
    //console.log(fun);io

}

function result(){
    
    let str= arr.join('');
    let answer = eval(str);
    console.log(answer);

    let demo = document.getElementById('display');
    demo.innerHTML= answer;

}

function allclear(){
    arr = [];
    let demo = document.getElementById('display');
    demo.innerHTML = arr;
}

function oneclear(){

     arr.pop();
     let demo = document.getElementById('display');
     demo.innerHTML = arr.join('');

    }
